@media only screen and (max-width: 415px) {
  .container .podium .podium-item {
    margin: 54px 30px 0;
  }
  .container .runner-ups {
    max-width: 542px;
  }
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Baloo 2', cursive;
}

html {
  min-width: 710px;
}

body {
  background: -webkit-gradient(linear, left bottom, left top, from(#fff1eb), to(#ace0f9)) no-repeat center center/cover;
  background: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%) no-repeat center center/cover;
}

.container {
  color: white;
  padding: 15px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.container h1 {
  margin: 0 0 20px 0;
  font-size: 3rem;
  font-weight: 600;
  text-align: center;
  text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.25);
  letter-spacing: 0.05em;
}

.podium {
  margin: 20px 27px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.podium .podium-item {
  margin: 54px 40px 0;
  text-align: center;
}

.podium .podium-item:nth-child(2) {
  margin-top: 0;
}

.podium .podium-item:nth-child(1) .poster::after {
  background-color: #DCDCDC;
}

.podium .podium-item:nth-child(2) .poster::after {
  background-color: #FFDC73;
}

.podium .podium-item:nth-child(3) .poster::after {
  background-color: #A77044;
}

.podium .podium-item .poster {
  width: 120px;
  height: 180px;
  margin: 0 auto 25px;
  position: relative;
  -webkit-box-shadow: 0px 0px 10px 4px rgba(51, 51, 51, 0.15);
          box-shadow: 0px 0px 10px 4px rgba(51, 51, 51, 0.15);
  border-radius: 5px;
}

.podium .podium-item .poster::after {
  content: attr(data-position);
  left: 50%;
  bottom: -25px;
  width: 50px;
  height: 50px;
  font-size: 2.8125rem;
  font-weight: 600;
  line-height: 55px;
  border-radius: 100%;
  position: absolute;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-box-shadow: 0px 0px 10px 4px rgba(51, 51, 51, 0.15);
          box-shadow: 0px 0px 10px 4px rgba(51, 51, 51, 0.15);
}

.podium .podium-item .name {
  font-size: 3rem;
  font-weight: 600;
  text-shadow: 2.5px 2.5px 0px rgba(0, 0, 0, 0.25);
}

.runner-ups {
  width: 100%;
  max-width: 592px;
}

.runner-ups .runner-up {
  margin: 48px 0 48px 32px;
  padding: 0 0 0 45px;
  position: relative;
  border-radius: 5px;
  -webkit-box-shadow: -4px 4px 0px #C4C4C4;
          box-shadow: -4px 4px 0px #C4C4C4;
  background-color: white;
}

.runner-ups .runner-up::after {
  content: attr(data-position);
  top: -15px;
  left: -28px;
  width: 48px;
  height: 51px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-size: 3rem;
  font-weight: 600;
  line-height: 58px;
  position: absolute;
  border-radius: 5px;
  -webkit-box-shadow: -4px 4px 0px #C4C4C4;
          box-shadow: -4px 4px 0px #C4C4C4;
  background-color: #ACE0F9;
}

.runner-ups .runner-up .name {
  color: #333333;
  font-size: 2.25rem;
  font-weight: 600;
  letter-spacing: 0.05em;
}
